@import url("https://fonts.googleapis.com/css2?family=Press+Start+2P&family=VT323&family=Bungee&family=Rubik:wght@400;500;700&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-gradient-to-br from-purple-500 via-pink-500 to-yellow-400 min-h-screen;
    background-attachment: fixed;
    margin: 0;
    font-family: "Rubik", "VT323", -apple-system, BlinkMacSystemFont, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
    position: relative;
  }

  body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: repeating-linear-gradient(
      to bottom,
      transparent 0px,
      transparent 2px,
      rgba(0, 0, 0, 0.05) 2px,
      rgba(0, 0, 0, 0.05) 4px
    );
    pointer-events: none;
    z-index: 1;
  }

  h1,
  h2,
  h3 {
    font-family: "Press Start 2P", cursive;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-shadow: 3px 3px 0 rgba(0, 0, 0, 0.2);
  }
}

@layer components {
  .btn-primary {
    @apply bg-gradient-to-r from-green-400 to-blue-500 hover:from-green-500 hover:to-blue-600 text-white font-bold rounded-lg px-6 py-3 transition-all duration-200 transform hover:scale-105 shadow-lg hover:shadow-xl;
    font-family: "Press Start 2P", cursive;
    font-size: 0.9rem;
    border: 3px solid #000;
    box-shadow: 4px 4px 0 #000;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.3);
    position: relative;
    overflow: hidden;
  }

  .btn-primary:active {
    transform: translate(4px, 4px);
    box-shadow: 0px 0px 0 #000;
  }

  .btn-primary::after {
    content: "";
    position: absolute;
    top: -50%;
    left: -60%;
    width: 20%;
    height: 200%;
    background: rgba(255, 255, 255, 0.3);
    transform: rotate(30deg);
    transition: all 0.6s;
  }

  .btn-primary:hover::after {
    left: 120%;
  }

  .btn-secondary {
    @apply bg-gradient-to-r from-pink-500 to-orange-500 hover:from-pink-600 hover:to-orange-600 text-white font-bold rounded-lg px-6 py-3 transition-all duration-200 transform hover:scale-105 shadow-lg hover:shadow-xl;
    font-family: "Press Start 2P", cursive;
    font-size: 0.9rem;
    border: 3px solid #000;
    box-shadow: 4px 4px 0 #000;
    text-shadow: 2px 2px 0 rgba(0, 0, 0, 0.3);
    position: relative;
    overflow: hidden;
  }

  .btn-secondary:active {
    transform: translate(4px, 4px);
    box-shadow: 0px 0px 0 #000;
  }

  .card {
    @apply bg-white/80 backdrop-blur-sm rounded-xl p-6 transition-all duration-200 border-4;
    border-style: solid;
    border-color: #000;
    box-shadow: 8px 8px 0 #000;
    position: relative;
    overflow: hidden;
  }

  .card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    background: linear-gradient(to right, #ff00cc, #3333ff, #00ff99);
    z-index: 2;
  }

  .floating {
    animation: floating 3s ease-in-out infinite;
  }

  .pulse {
    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
  }

  .bounce {
    animation: bounce 1s infinite;
  }

  .glitch {
    animation: glitch 1s linear infinite;
    position: relative;
  }

  .pixel-border {
    border-style: solid;
    border-width: 4px;
    border-color: #000;
    box-shadow: 6px 6px 0 #000;
    position: relative;
  }

  .game-title {
    font-family: "Press Start 2P", cursive;
    font-size: 2.5rem;
    background: linear-gradient(to right, #ff00cc, #3333ff, #00ff99);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 4px 4px 0 rgba(0, 0, 0, 0.2);
    letter-spacing: -1px;
    animation: rainbow-shift 3s linear infinite;
  }
}

@keyframes floating {
  0% {
    transform: translateY(0px) rotate(0deg);
  }
  25% {
    transform: translateY(-15px) rotate(-2deg);
  }
  50% {
    transform: translateY(0px) rotate(0deg);
  }
  75% {
    transform: translateY(-7px) rotate(2deg);
  }
  100% {
    transform: translateY(0px) rotate(0deg);
  }
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(0.95);
  }
}

@keyframes bounce {
  0%,
  100% {
    transform: translateY(-10%);
    animation-timing-function: cubic-bezier(0.8, 0, 1, 1);
  }
  50% {
    transform: translateY(0);
    animation-timing-function: cubic-bezier(0, 0, 0.2, 1);
  }
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-2px, 2px);
  }
  40% {
    transform: translate(-2px, -2px);
  }
  60% {
    transform: translate(2px, 2px);
  }
  80% {
    transform: translate(2px, -2px);
  }
  100% {
    transform: translate(0);
  }
}

@keyframes rainbow-shift {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}

/* Custom scrollbar */
::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: #222;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  background-clip: padding-box;
}

::-webkit-scrollbar-thumb {
  @apply bg-gradient-to-b from-pink-500 to-purple-600;
  border-radius: 20px;
  border: 3px solid transparent;
  background-clip: padding-box;
}

::-webkit-scrollbar-thumb:hover {
  @apply bg-gradient-to-b from-pink-600 to-purple-700;
}

code {
  font-family: "VT323", source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  background: #222;
  color: #0f0;
  padding: 2px 4px;
  border-radius: 4px;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.7);
}
